import {Component, OnDestroy, OnInit} from '@angular/core';
import {ThemeService} from '../../shared/service/theme.service';
import {ThemeResponse} from '../../shared/model/ThemeResponse';
import {Subscription} from 'rxjs/Subscription';

@Component({
  selector: 'app-admin-sub',
  templateUrl: './admin-sub.component.html',
  styleUrls: ['./admin-sub.component.css']
})
export class AdminSubComponent implements OnInit, OnDestroy {

  themes: ThemeResponse;
  busy: Subscription;
  themSize: number;

  constructor(private themeService: ThemeService) {
    this.themeService.getAndSetTheme();
  }

  ngOnInit() {
    this.themes = new ThemeResponse;
    this.busy = this.themeService.getAllThemes().subscribe(res => {
      this.themes = res;
      console.log('size in admin-sub == ' + res);
      this.themSize = this.themes.themes.length;
    }, error => {
      this.themSize = 0;
      console.log('size ERROR in admin-sub == ' + error);
    });
  }

  ngOnDestroy(): void {
    this.busy.unsubscribe();
  }

}



