import {Component, Input, OnInit} from '@angular/core';
import { ROUTES } from './sidebar-routes.config';
import { MenuType } from './sidebar.metadata';
import {UserAuthService} from '../shared/service/user-auth.service';
import {ChurchResponse} from '../shared/model/ChurchResponse';

declare var $: any;

@Component({
    moduleId: module.id,
    selector: 'app-sidebar',
    templateUrl: 'sidebar.component.html',
})

export class SidebarComponent implements OnInit {

  @Input() role: any;
    public menuItems: any[];
    private churchInfo: ChurchResponse;
    churchName: string;
    churchAddress: string;

  constructor(private churchService: UserAuthService) {
    /*this.churchName = this.churchService.getLoggedChurch().data.creator.church_name;
     this.churchAddress = this.churchService.getLoggedChurch().data.creator.church_address;*/
    /*this.churchName = this.churchInfo.data.creator.church_name;
     this.churchAddress = this.churchInfo.data.creator.church_address;*/
    // console.log('logged church info status in sidebar == ' + this.churchInfo.status);
  }

    ngOnInit() {
      this.churchInfo = new ChurchResponse;
        $.getScript('../../assets/js/sidebar-moving-tab.js');

        this.menuItems = ROUTES.filter(menuItem => menuItem.menuType !== MenuType.BRAND);
      // this.churchInfo = this.churchService.getLoggedChurch();
      this.churchService.getLoggedChurch().subscribe(res => {
        this.churchInfo = res;
        this.churchName = this.churchInfo.user.church_name;
        this.churchAddress = this.churchInfo.user.church_address;
      });
      console.log('passed admin role == ' + this.role);
    }
}



