import {Injectable, OnDestroy} from '@angular/core';
import {Http, RequestOptions, Response, Headers} from '@angular/http';
import {ThemeModel} from '../model/ThemeModel';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import {ThemeResponse} from '../model/ThemeResponse';
import {ReplaySubject} from 'rxjs/ReplaySubject';
import {Subscription} from 'rxjs/Subscription';
import {AppSettings} from '../AppSettings';

export class UpdImgRes {
  image_path: any;
  image_url: any;
}

class CreateThemeModel {
  name: any;
  image_url: any;
  jenkins_job_url: any;
  first_menu: any;
  second_menu: any;
  third_menu: any;
  fourth_menu: any;
  fifth_menu: any;
}

class SingleThemeResponse {
  theme: ThemeModel;
}

@Injectable()
export class ThemeService implements OnDestroy {

  private registrationUrl= 'http://api.smartapp.tk/api/v1/creator/auth/register';
  private loginUrl= 'http://api.smartapp.tk/api/v1/creator/auth/login';
  private all_theme_url= AppSettings.API_ENDPOINT + '/theme';
  private theme_img_url= AppSettings.API_ENDPOINT + '/image/upload';
  private headers;
  private options;
  private theme: ThemeModel;
  themeErr: any;
  private delTheme: ThemeModel;
  private themeResp: ThemeResponse;
  crTheme: CreateThemeModel;
  imgRes: UpdImgRes;
  sub: Subscription;
  private subject = new ReplaySubject<any>(1);
  public themeArrSubject: ReplaySubject<ThemeResponse> = new ReplaySubject<ThemeResponse>(1);
  // public bSubject: Subject<ThemeResponse> = new BehaviorSubject<>(ThemeResponse);

  constructor(protected http: Http) {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    this.options = new RequestOptions({
      headers: this.headers});
    this.theme = new ThemeModel;
    this.delTheme = new ThemeModel;
    this.crTheme = new CreateThemeModel;
    this.imgRes = new UpdImgRes;
    this.themeResp = new ThemeResponse;
  }

  getUpdatedTheme(): Observable<ThemeResponse> {
    // ...using get request
    return this.http.get(this.all_theme_url)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  getAndSetTheme() {
    // this.sub = this.getUpdatedTheme().subscribe(resp => {
    this.getUpdatedTheme().subscribe(resp => {
      // this.themeResp = resp;
      this.themeArrSubject.next(resp);
      // this.themeArrSubject.complete();
    }, (error: any) => {
      this.themeArrSubject.error(error);
      this.themeErr = error;
    });
  }

  getAllThemes (): Observable<ThemeResponse> {
    return this.themeArrSubject.asObservable();
  }

  getThemeById (id: any): Observable<SingleThemeResponse> {
    // ...using get request
    return this.http.get(this.all_theme_url + '/' + id)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }


  private extractData(res: Response) {
    const body = res.json();
    return body;
  }

  private handleErrorObservable (error: Response | any) {
    console.error(error.message || error);
    return Observable.throw(error.message || error);
  }

  // set theme to be deleted
  setThemeDel(themeDel: ThemeModel) {
    this.delTheme = themeDel;
    this.subject.next(themeDel);
  }

  // get theme to be deleted
  getDelTheme (): Observable<any> {
    return this.subject.asObservable();
  }

  // Add a new Theme
  addTheme (body: Object): Observable<ThemeResponse> {
    const bodyString = JSON.stringify(body); // Stringify payload
    return this.http.post(this.all_theme_url, body, this.options) // ...using post request
      .map((res: Response) => res.json()) // ...and calling .json() on the response to return data
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  // Update a Theme
  updateTheme (id: any, themeName: any, themeJenkins: any, firstMenu: string, secondMenu: string,
               thirdMenu: string, fourthMenu: string, fifthMenu: string, file: File) {

    const formData: FormData = new FormData();
    formData.append('image', file, file.name);
    const headers = new Headers();
    console.log('response for image upload THEMESERVICE == ' + file.name);
    /** No need to include Content-Type in Angular 4 */
    headers.append('enctype', 'multipart/form-data');
    const options = new RequestOptions({ headers: headers });
    return this.http.post(this.theme_img_url, formData)
      .map((res: Response) => {
        this.imgRes = res.json();
        console.log('gotten image res inside service == ' + this.imgRes.image_url);
        this.crTheme.name = themeName;
        this.crTheme.image_url = this.imgRes.image_url;
        this.crTheme.jenkins_job_url = themeJenkins;
        this.crTheme.first_menu = firstMenu;
        this.crTheme.second_menu = secondMenu;
        this.crTheme.third_menu = thirdMenu;
        this.crTheme.fourth_menu = fourthMenu;
        this.crTheme.fifth_menu = fifthMenu;
      })
      .flatMap((body) => {
        console.log('body in flatmap == ' + body + ' jsonified body == ' + JSON.stringify(body));
        return this.http.put(this.all_theme_url + '/' + id, JSON.stringify(this.crTheme), this.options)
          .map((resp: Response) => resp.json())
          .catch((error: any) => Observable.throw(error.json().error || 'Server in flatmap error'));
      }) // ...and calling .json() on the response to return data
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));


    /*const bodyString = JSON.stringify(body); // Stringify payload
    console.log('updt body jsonStringify == ' + bodyString);
    return this.http.put(this.all_theme_url + '/' + id, bodyString, this.options) // ...using put request
      .map((res: Response) => res.json()) // ...and calling .json() on the response to return data
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));*/
  }

  // Delete a theme
  removeTheme (id: string): Observable<ThemeResponse> {
    return this.http.delete(`${this.all_theme_url}/${id}`) // ...using put request
      .map((res: Response) => res.json()) //
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  addNewTheme (themeName: any, themeJenkins: any, firstMenu: string, secondMenu: string,
               thirdMenu: string, fourthMenu: string, fifthMenu: string, file: File) {
    const formData: FormData = new FormData();
    formData.append('image', file, file.name);
    const headers = new Headers();

    /** No need to include Content-Type in Angular 4 */
    headers.append('enctype', 'multipart/form-data');
    const options = new RequestOptions({ headers: headers });
    return this.http.post(this.theme_img_url, formData)
      .map((res: Response) => {
      this.imgRes = res.json();
        console.log('gotten image res inside service == ' + this.imgRes.image_path);
        this.crTheme.name = themeName;
        this.crTheme.image_url = this.imgRes.image_path;
        this.crTheme.jenkins_job_url = themeJenkins;
        this.crTheme.first_menu = firstMenu;
        this.crTheme.second_menu = secondMenu;
        this.crTheme.third_menu = thirdMenu;
        this.crTheme.fourth_menu = fourthMenu;
        this.crTheme.fifth_menu = fifthMenu;
      })
      .flatMap((body) => {
      console.log('body in flatmap == ' + body + ' jsonified body == ' + JSON.stringify(body));
      return this.http.post(this.all_theme_url, JSON.stringify(this.crTheme), this.options)
        .map((resp: Response) => resp.json())
        .catch((error: any) => Observable.throw(error.json().error || 'Server in flatmap error'));
      }) // ...and calling .json() on the response to return data
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));

    /*const options = new RequestOptions({ headers: headers });
    return this.http.post(this.theme_img_url, formData)
      .map((res: Response) => res.json()) // ...and calling .json() on the response to return data
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));*/
  }


  ngOnDestroy(): void {
    if (this.sub != null) {
      this.sub.unsubscribe();
    }
  }

}
