import { Injectable } from '@angular/core';
import {Http, Response, RequestOptions, Headers} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import {ChurchModel} from '../model/ChurchModel';
import {ChurchResponse} from '../model/ChurchResponse';
import {AppSettings} from '../AppSettings';
import {ReplaySubject} from 'rxjs/ReplaySubject';

@Injectable()
export class UserAuthService {

  private registrationUrl= 'http://api.smartapp.tk/api/v1/creator/auth/register';
  private newRegUrl= AppSettings.API_ENDPOINT + '/auth/register';
  private newLoginUrl= AppSettings.API_ENDPOINT + '/auth/login';
  private loginUrl= 'http://api.smartapp.tk/api/v1/creator/auth/login';
  private headers;
  private options;
  public isLoggedIn: boolean;
  private isAdmin: boolean;
  private loggedChurch: ChurchResponse;
  loggedChurchSub: ReplaySubject<ChurchResponse> = new ReplaySubject<ChurchResponse>(1);

  constructor(protected http: Http) {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    this.options = new RequestOptions({
      headers: this.headers});
    this.isLoggedIn = true;
    this.isAdmin = false;
    this.loggedChurch = new ChurchResponse();
  }

  registerUser(data: ChurchModel): Observable<any> {

    const body = JSON.stringify(data);
    console.log('strinigfied data == ' + body + ' church string == ' + data.email);
    return this.http.post(this.newRegUrl, body, this.options)
      .map((res: Response) => res.json())
      .catch ((error: any) => Observable.throw(error.json().error || 'Server error'));

  }

  loginUser(email: any, pwd: any): Observable<ChurchResponse> {

    const body = JSON.stringify({'email': email, 'password': pwd});
    console.log('login user inside service == ' + body);
    return this.http.post(this.newLoginUrl, body, this.options)
      .map((res: Response) => res.json())
      .catch ((error: any) => Observable.throw(error.json().error || 'Server error'));

  }

  setLoggedChurch(church: ChurchResponse) {
    console.log('inside setLogged in auth service email == ' + church.user.church_name);
    this.loggedChurch = church;
    this.loggedChurchSub.next(church);
    this.isLoggedIn = true;
  }

  getLoggedChurch() {
    return this.loggedChurchSub.asObservable();
  }

  setIsAdmin(role: any) {
    if (role === 1) {
      this.isAdmin = true;
    } else {
      this.isAdmin = false;
    }
  }

  getIsAdmin ( ) {
    return this.isAdmin;
  }

  setIsLoggedIn () {
    // this.isLoggedIn = true;
    return this.isLoggedIn;
  }
  /*test params for login
  email = femibami@mail.net
  pwd = password1
  address = yaba
  web = android*/
}



