import {Component, OnChanges, OnDestroy, OnInit, ViewContainerRef} from '@angular/core';
import {MenuService} from '../../shared/service/menu.service';
import {MenuModel} from '../../shared/model/MenuModel';
import {Subscription} from 'rxjs/Subscription';
import {FileModel} from '../../shared/model/FileModel';
import {TextContentModel} from '../../shared/model/TextContentModel';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {MenuPostModel} from '../../shared/model/MenuPostModel';
import {SubMenuPostModel} from '../../shared/model/SubMenuPostModel';
import {AppModel} from '../../shared/model/AppModel';
import {MenuResponse} from '../../shared/model/MenuResponse';
import {Submenu2Object, SubMenuResponse2} from '../../shared/model/SubMenuResponse2';
import {ThemeModel} from '../../shared/model/ThemeModel';
import {NotifyService} from '../../shared/service/notify.service';
import {AppInfoService} from '../../shared/service/app-info.service';
import {ActivatedRoute, Router} from '@angular/router';
import {ThemeService} from '../../shared/service/theme.service';
import {SubMenuResponse} from '../../shared/model/SubMenuResponse';


@Component({
  selector: 'app-builder-sub-four',
  templateUrl: './builder-sub-four.component.html',
  styleUrls: ['./builder-sub-four.component.css']
})
export class BuilderSubFourComponent implements OnInit, OnChanges, OnDestroy {


  private sub: Subscription;
  menu_name: string;
  menu_icon: string;
  menu_model: MenuModel;
  showHide: boolean;
  menuSelected2: boolean;
  listItems: string[] = [];
  subMenuList2: string[] = [];
  fileModel: FileModel;
  textModel: TextContentModel;
  busy: Subscription;
  listForm: FormGroup;
  subMenuSub: Subscription;
  fileSub: Subscription;
  newMenuName: any;
  menuPostModel: MenuPostModel;
  submenuPModel: SubMenuPostModel;
  appModel: AppModel;
  submenuArr: any[] = [];
  submenuArr2: any[] = [];
  menuResponse: MenuResponse;
  top_menu_id: any;
  top_menu_name: any;
  subMenuRes: SubMenuResponse2;
  fileMenuName: any;
  activeSubIndex: any;
  isEditMode: boolean;
  editedFileType: any;
  editFileForm: FormGroup;
  menu1Index: any;
  emptyState: boolean;
  fileSaveStatus: boolean;
  mThemeModel: ThemeModel;
  menuAdded: boolean;

  fileTypes: any[] = [{mName: 'Audio', mValue: 'audio'}, {mName: 'Video', mValue: 'video'},
    {mName: 'Text', mValue: 'text'}, {mName: 'Url', mValue: 'url'}];

  constructor(private menuService: MenuService, private fb: FormBuilder,
              private notify: NotifyService, private vRef: ViewContainerRef,
              private ais: AppInfoService, private route: Router, private aRoute: ActivatedRoute,
              private themeService: ThemeService) {
    this.listForm = this.fb.group({
      listField: [null]
    });
    this.editFileForm = this.fb.group({
      fileName: [null],
      fileContent: [null]
    });
  }

  ngOnInit() {
    this.menuService.getMenuModel().subscribe(res => {
      this.menu_model = res;
      this.menu_name = this.menu_model.menu_name;
      this.menu_icon = this.menu_model.menu_icon;
      console.log('returned menu modelled object == ' + JSON.stringify(res));
    });
    this.showHide = false;
    this.menuSelected2 = false;
    this.isEditMode = false;
    this.fileSaveStatus = false;
    this.emptyState = true;
    this.menuPostModel = new MenuPostModel;
    this.submenuPModel = new SubMenuPostModel;
    // this.fileModel = new FileModel;
    this.editedFileType = 'audio';
    this.textModel = new TextContentModel;
    this.appModel = new AppModel;
    this.appModel = this.ais.getAppInfo();
    this.getMenuFromServer();
    this.getSubmenuArr();
    this.getSubmenuArrayL2();
    this.getFileFromObservable();
    this.getSubmenu2Observables();
    // this.getSubmenuArr();
  }

  ngOnChanges(): void {
  }

  ngOnDestroy(): void {
    if (this.subMenuSub != null) {
      this.subMenuSub.unsubscribe();
    }
    if (this.busy != null) {
      this.busy.unsubscribe();
    }
  }

  startEditMode() {
    this.isEditMode = true;
  }

  doneEditMode(fileName, fileContent) {
    this.isEditMode = false;
    const editedFile = new FileModel;
    editedFile.name = fileName;
    editedFile.content = fileContent;
    editedFile.fileType = this.editedFileType;
    this.menuService.setObservableFile(editedFile);
    console.log('selected option jsonStrinigfy == ' + JSON.stringify(editedFile)
      + ' editted name and con == ' + fileName + ' cc == ' + fileContent);
  }



  deleteSubmenu(id: any, menuId) {
    console.log('passed array id DELETE == ' + id + ' Menu id == ' + menuId);
    // this.listItems.splice(id, 1);
    this.menuService.deleteMenuFromServer(menuId).subscribe(res => {
      if (menuId === this.top_menu_id) {
        this.fileModel = null;
      }
      this.menuResponse.subMenus.splice(id, 1);
      if (this.menuResponse.subMenus.length <= 0) {
        this.menuResponse = null;
        // this.menuAdded = false;
        this.prepareNextMenu();
      }
      /*if (this.subMenuRes.menu.length > 0) {
       if (this.subMenuRes.menu)
       }*/
    });
  }

  deleteSubmenu2(index: any, menuId) {
    console.log('passed array id DELETE == ' + index + ' Menu id == ' + menuId);
    this.menuService.deleteSubmenu2FromServer(menuId).subscribe(res => {
      if (index === this.top_menu_id) {
        this.fileModel = null;
      }
      this.subMenuRes.menu.splice(index, 1);
    });
  }


  selectedSubmenu1(submenu: SubMenuResponse, submenu1: any) {
    // console.log('selected submenu == ' + 'sub idd == ' + submenu.id + ' passed TOPID == ' + topId);
    this.top_menu_id = submenu.id;
    this.top_menu_name = submenu.name;
    this.menu1Index = submenu1;
    this.fileMenuName = submenu.name;
  }

  getSubmenuArr() {
    this.subMenuSub = this.menuService.getSubMenuArrayL1().subscribe(subArray => {
      this.listItems = subArray;

      this.submenuArr = [];
      // loop thru the array, build each elemant into the submenu model
      for (let a = 0; a < this.listItems.length; a++) {
        const subObject = {
          'name': this.listItems[a],
          'content': '',
          'content_type': '',
          'appid': this.appModel.appId
        };
        this.submenuArr.push(subObject);
      }
      this.menuPostModel.appid = this.appModel.appId;
      this.menuPostModel.name = this.newMenuName;
      this.menuPostModel.creator_email = this.appModel.creator_email;
      this.menuPostModel.submenus = this.submenuArr;
      this.sendMenuToServer(this.menuPostModel);
    });
    console.log('list on init BUILDER SUB ONE == ' + this.listItems);
  }

  getSubmenuArrayL2() {
    this.subMenuSub = this.menuService.getSubMenuArrayL2().subscribe(subArray => {
      this.subMenuList2 = subArray;
      this.submenuArr2 = [];
      // loop thru the array, build each elemant into the submenu model
      for (let b = 0; b < this.subMenuList2.length; b++) {
        const subObject = {
          'name': this.subMenuList2[b],
          'content': 'list',
          'content_type': 'list',
          'appid': this.appModel.appId,
          'top_menu_id': this.top_menu_id
        };
        this.submenuArr2.push(subObject);
      }
      this.menuService.setObservableSub(this.submenuArr2);
      // this.sendSubMenu2ToServer(this.submenuArr2);
    });
  }

  sendMenuToServer(menuObj: any) {
    /*this.menuPostModel.appid = this.appModel.appId;
     this.menuPostModel.name = this.newMenuName;
     this.menuPostModel.creator_email = this.appModel.appCreatorEmail;
     this.menuPostModel.submenus = this.submenuArr;*/
    this.menuService.setMenu1Obs(menuObj);
  }

  getMenuFromServer() {
    this.menuService.getMenu1Obs().subscribe(res => {
      this.menuResponse = res;
      this.menuAdded = true;
      this.emptyState = false;
      if (this.menuResponse.subMenus.length <= 0) {
        this.menuResponse = null;
        console.log('getMenuFromServer inside menu lenght check');
      }
    }, err => {
      console.log('error in sending menu to server == ' + err);
      this.notify.showError('Please Check your Network and try again ' + '\n' + err, 'An Error occurred', this.vRef);
    });
  }

  getSubmenu2Observables() {
    this.menuService.getSubmenuObs().subscribe(res => {
      this.subMenuRes = res;
      console.log('menu response before split #BuilderSubOneComponent == ' + JSON.stringify(res));
      if (this.subMenuRes.menu.length <= 0) {
        this.subMenuRes = null;
        // at this point remove the top menu item too
        // this.menuResponse.subMenus.splice(this.menu1Index, 1);
        this.removeMenu();
        console.log('menu response after split #BuilderSubOneComponent == ' + JSON.stringify(this.menuResponse));
        // this.menuService.setMenu1Obs()
      }
    }, err => {
      console.log('error in sending menu to server == ' + err);
      this.notify.showError('Please Check your Network and try again ' + '\n' + err, 'An Error occurred', this.vRef);
    });
  }

  addContent () {
    console.log('new menu name == ' + this.newMenuName);
  }

  addFileToSubmenu2(submenu: Submenu2Object, index: any) {
    console.log('submenu iD sent as top menu id == ' + submenu.id + ' with index == ' + index);
    this.top_menu_id = submenu.id;
    this.fileMenuName = submenu.name;
    this.activeSubIndex = index;
  }

  getFileFromObservable() {
    this.fileSub = this.menuService.getObservableFile().subscribe(res => {
      this.fileModel = res;
      this.menuAdded = true;
      if (this.top_menu_id == null) {
        this.fileMenuName = this.newMenuName;
      }
      this.emptyState = false;
      this.editedFileType = this.fileModel.fileType;
      (<FormControl>this.editFileForm.controls['fileName']).setValue(this.fileModel.name, { onlySelf: true });
      (<FormControl>this.editFileForm.controls['fileContent']).setValue(this.fileModel.content, { onlySelf: true });
    } );
  }

  saveFileToServer(fileName, fileContent) {
    const fileArr: any[] = [];

    // file is being added to a submenu if TOP MENU ID does not exist
    if (this.top_menu_id != null) {
      const subObject = {
        'name': fileName,
        'content': fileContent,
        'content_type': this.editedFileType,
        'appid': this.appModel.appId,
        'top_menu_id': this.top_menu_id
      };
      fileArr.push(subObject);
      this.busy = this.menuService.postSubMenuToServer(fileArr).subscribe(res => {
        console.log('response on sending file to server #BuilderSubOneComponent == ' + JSON.stringify(res));
        // on successful push, remove the submenu from the arraylist
        if (this.subMenuRes != null) {
          console.log('inside submenu null checker');
          this.removeSubmenu();
        } else {
          console.log('inside Menu null checker');
          this.removeMenu();
        }
        this.fileSaveStatus = true;
      }, err => {
        console.log('error in sending menu to server == ' + err);
        this.notify.showError('Please Check your Network and try again ' + '\n' + err, 'An Error occurred', this.vRef);
      });
    } else {
      // the file is to be added to a menu item
      const subObject = {
        'name': fileName,
        'content': fileContent,
        'content_type': this.editedFileType,
        'appid': this.appModel.appId
      };
      fileArr.push(subObject);
      this.menuPostModel.appid = this.appModel.appId;
      this.menuPostModel.name = this.newMenuName;
      this.menuPostModel.creator_email = this.appModel.creator_email;
      this.menuPostModel.submenus = fileArr;

      this.menuService.postMenuToServer(this.menuPostModel).subscribe(mRes => {
        console.log('hot-linked file response == ' + JSON.stringify(mRes));
        this.prepareNextMenu();
      });

    }

    console.log('response on saving file to server #BuilderSubOneComponent == ' + JSON.stringify(fileArr));
  }

  removeMenu () {
    this.menuResponse.subMenus.splice(this.menu1Index, 1);
    this.fileModel = null;
    this.notify.showSuccess('File save Successfully' + '\nAssociated menu closed for editing' , 'Success', this.vRef);
    this.checkMenuEditingState();
  }

  removeSubmenu() {
    console.log('inside remove submenu');
    this.subMenuRes.menu.splice(this.activeSubIndex, 1);
    this.menuService.subMenuObs.next(this.subMenuRes);
    this.fileModel = null;
    this.notify.showSuccess('File save Successfully' + '\nAssociated menu closed for editing' , 'Success', this.vRef);
  }

  // this method checks if all added menus have been edited and auto-navigates to the NEXT MENU
  checkMenuEditingState() {
    console.log('checkmenuEditing state inside fileStatus check' + ' submenu lenght == ' + this.menuResponse.subMenus.length);
    if (this.menuResponse.subMenus.length <= 0) {
      this.menuResponse = null;
      if (this.fileSaveStatus) {
        this.prepareNextMenu();
      }
    }
  }

  prepareNextMenu() {
    this.busy = this.themeService.getThemeById(this.appModel.themeid).subscribe(res => {
      this.mThemeModel = res.theme;
      this.menuService.setMenuModel(this.mThemeModel.fifth_menu, 'more_vert', 'event contents');
      this.route.navigate(['./page_five'], {relativeTo: this.aRoute.parent});
    });
  }
}



