interface MenuNames {
  name: string;
  formCtrlName: string;
}

@Component({
  selector: 'app-admindashboard',
  templateUrl: './admindashboard.component.html',
  styleUrls: ['./admindashboard.component.css']
})
export class AdmindashboardComponent implements OnInit, OnDestroy {

  isAdmin: any;
  themeForm: FormGroup;
  editThemeForm: FormGroup;
  busy: Subscription;
  sub: Subscription;
  themeDele: ThemeModel;
  upd: UpdImgRes;
  changePhoto: boolean;
  selectedFile: File;
  menuNames: MenuNames[] = [
  {name: 'Home', formCtrlName: 'tMenu1'},
  {name: 'Events', formCtrlName: 'tMenu2'},
  {name: 'Messages', formCtrlName: 'tMenu3'},
  {name: 'Give', formCtrlName: 'tMenu4'},
  {name: 'More', formCtrlName: 'tMenu5'}
];


  constructor(private userService: UserAuthService, private fb: FormBuilder,
              private themeService: ThemeService, private router: Router,
              private aRoute: ActivatedRoute, private notify: NotifyService, private vRef: ViewContainerRef) {
    this.themeForm = this.fb.group({
    themeNameCtr: [null, Validators.required],
    themeCodeUrlCtr: ['http://', Validators.required],
    tMenu1: [null, Validators.required],
    tMenu2: [null, Validators.required],
    tMenu3: [null, Validators.required],
    tMenu4: [null, Validators.required],
    tMenu5: [null, Validators.required],
    imgCtr: [null, Validators.required]
  });

    this.editThemeForm = this.fb.group({
      name: ['', Validators.required],
    etCodeUrlCtr: ['', Validators.required],
    etMenu1: [null, Validators.required],
    etMenu2: [null, Validators.required],
    etMenu3: [null, Validators.required],
    etMenu4: [null, Validators.required],
    etMenu5: [null, Validators.required],
    etImgCtr: [null, Validators.required]
  });
    this.sub = this.themeService.getDelTheme().subscribe(tema => {
      this.themeDele = tema;
      console.log('passed tema == ' + tema);
      (<FormControl>this.editThemeForm.controls['name']).setValue(this.themeDele.name, { onlySelf: true });
      (<FormControl>this.editThemeForm.controls['etCodeUrlCtr']).setValue(this.themeDele.jenkins_job_url, { onlySelf: true });
      (<FormControl>this.editThemeForm.controls['etMenu1']).setValue(this.themeDele.first_menu, { onlySelf: true });
      (<FormControl>this.editThemeForm.controls['etMenu2']).setValue(this.themeDele.second_menu, { onlySelf: true });
      (<FormControl>this.editThemeForm.controls['etMenu3']).setValue(this.themeDele.third_menu, { onlySelf: true });
      (<FormControl>this.editThemeForm.controls['etMenu4']).setValue(this.themeDele.fourth_menu, { onlySelf: true });
      (<FormControl>this.editThemeForm.controls['etMenu5']).setValue(this.themeDele.fifth_menu, { onlySelf: true });
      (<FormControl>this.editThemeForm.controls['etImgCtr']).setValue(this.themeDele.image_url, { onlySelf: true });
    });
  }

  ngOnInit() {
    this.isAdmin = this.userService.getIsAdmin();
    this.upd = new UpdImgRes;

  }


  ngOnDestroy(): void {
    if (this.busy != null) {
      this.busy.unsubscribe();
    }
    if (this.sub != null) {
      this.sub.unsubscribe();
    }
  }

  etChangePhoto () {
    this.changePhoto = true;
    this.editThemeForm.controls['etImgCtr'].setValue('');
  }

  cancelPhotoChange() {
    this.changePhoto = false;
  }

  imageUploaded(event: any) {
    console.log('error response after upload == ' + event.file);
    this.selectedFile = event.file;
    this.themeForm.controls['imgCtr'].setValue(this.selectedFile);

    // (<FormControl>this.editThemeForm.controls['name']).setValue(this.themeDele.name, { onlySelf: true });
  }

  editThemeImage (event: any) {
    console.log('error response after upload == ' + event.file);
    this.selectedFile = event.file;
    this.editThemeForm.controls['etImgCtr'].setValue(this.selectedFile);
    this.changePhoto = true;
  }

  disableSendButton(event: any) {
    console.log('response in disable send button == ' + event);
  }

  createTheme(themeName: any, themeJenkinsUrl: any, firstMenu: string, secondMenu: string,
              thirdMenu: string, fourthMenu: string, fifthMenu: string) {
    this.busy = this.themeService.addNewTheme(themeName, themeJenkinsUrl, firstMenu,
      secondMenu, thirdMenu, fourthMenu, fifthMenu, this.selectedFile)
      .subscribe(res => {
        this.notify.showSuccess('New Theme ' + themeName + ' successfully added', 'Success', this.vRef);
        this.themeService.getAndSetTheme();
        /*setTimeout(() => {
          // this.router.navigate(['themes'], {relativeTo: this.aRoute.firstChild});
          // this.router.navigate(['/admin/dashboard/themes']);
        }, 3000);*/
    }, err => {
        this.notify.showError(err, 'An Error occurred', this.vRef);
      console.log('error response after upload == ' + err);
    });
  }

  editTheme(themeName: any, themeJenkinsUrl: any, firstMenu: string, secondMenu: string,
            thirdMenu: string, fourthMenu: string, fifthMenu: string) {

    this.busy = this.themeService.updateTheme(this.themeDele.id, themeName, themeJenkinsUrl, firstMenu,
      secondMenu, thirdMenu, fourthMenu, fifthMenu, this.selectedFile)
      .subscribe(res => {
        this.notify.showSuccess('Theme successfully updated', 'Success', this.vRef);
        this.themeService.getAndSetTheme();
      }, err => {
        this.notify.showError(err, 'An Error occurred', this.vRef);
        console.log('error response after upload == ' + err);
      });
    this.changePhoto = false;

    // this.editThemeForm.reset();
    /*this.busy = this.themeService.updateTheme(theme.id, this.mTheme).subscribe(res => {
      this.notify.showSuccess('Theme Successfully updated', 'Success', this.vRef);
      console.log('successful update == ' + res);
    }, err => {
      console.log('error on update == ' + err);
      this.notify.showError(err, 'An Error Occurred', this.vRef);
    });*/
  }

  deleteTheme(thema: ThemeModel) {
    console.log('theme to be del == ' + thema.name + ' themadel == ' + thema.id);
    this.busy = this.themeService.removeTheme(thema.id).subscribe(res => {
      console.log('on successful deletion == ' + res + ' json stringifid == ' + JSON.stringify(res));
      // this.router.navigate(['/admin/dashboard/themes']);
      // this.router.navigate(['themes'], {relativeTo: this.aRoute.firstChild});
      this.themeService.getAndSetTheme();
    });
  }
}




