import {Component, OnDestroy, OnInit, ViewContainerRef} from '@angular/core';
import {FormBuilder, FormControlName, FormGroup, Validators} from '@angular/forms';
import {Subscription} from 'rxjs/Subscription';
import {UserAuthService} from '../shared/service/user-auth.service';
import {Router} from '@angular/router';
import {ToastsManager} from 'ng2-toastr';
import {ChurchModel} from '../shared/model/ChurchModel';
import {ChurchResponse} from '../shared/model/ChurchResponse';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit, OnDestroy {

  registerForm: FormGroup;
  chkbox: boolean;
  webText: string;
  cWebAddress: string;
  busy: Subscription;
  church: ChurchModel;
  cUser: ChurchResponse;
  hasErrors: boolean;
  errorText: any;

  constructor(public fb: FormBuilder, private registerService: UserAuthService,
              private router: Router, private toastr: ToastsManager, vRef: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vRef);

    this.registerForm = this.fb.group({
      emailField: [null, [Validators.required, Validators.email]],
      // emailField: [null, [Validators.required, Validators.pattern('[^ @]*@[^ @]*')]],
      pwdField: [null, [Validators.required, Validators.minLength(8)]],
      fName: [null, Validators.required],
      lName: [null, Validators.required],
      churchName: [null, Validators.required],
      cWebAddress: ['http://'],
      cAddress: [null, Validators.required],
      chkbox: [null]
    });
  }

  ngOnInit() {
    this.webText = 'http://';
    this.church = new ChurchModel;
    this.registerService.getLoggedChurch().subscribe(res => {
      this.cUser = res;
    });
    this.hasErrors = false;
  }

  checkee(sc: any) {
    console.log('check state == ' + this.registerForm.controls['cWebAddress'].value);
  }


  registerUser(fName, lName, email, pwd, cName, cWeb, cAddr) {
    this.church.first_name = fName;
    this.church.last_name = lName;
    this.church.email = email;
    this.church.password = pwd;
    this.church.church_name = cName;
    this.church.church_address = cAddr;
    this.church.church_website = cWeb;
    this.church.role = 1;

    this.hasErrors = false;

    console.log('church json b4 reg == ' + JSON.stringify(this.church));

    this.busy = this.registerService.registerUser(this.church).subscribe(res => {
        // this.submitPending = true;
        console.log('Error status code json = ' + res.json + ' response w/o json == ' + res);
        // this.showSuccess('Registration Successful', 'success', res.status);
        this.performSilentLogin(this.church.email, this.church.password);
      },
      error => {
        this.showError('Could not create your Account', 'An Error occurred');
        // this.submitPending = false;
        this.errorText = error;
        this.hasErrors = true;
        console.log('checking church sent = ' + JSON.stringify(this.church));
        console.log('Error status code json = ' + error);
        // this.loadAppInfo();
      });

  }

  showSuccess(text: string, title: string) {
    this.toastr.success(text, title, {toastLife: 3000, showCloseButton: false});
    setTimeout(() => {
      if (this.cUser.user.role === 1) {
        this.router.navigate(['./admin']);
      } else {
        this.router.navigate(['']);
      }
    }, 3000);  // 5s
    // this.router.navigate(['']);
  }

  showError(text: string, title: string) {
    this.toastr.error(text, title);
  }

  performSilentLogin(email: any, password: any) {
    this.registerService.loginUser(email, password).subscribe(res => {
        console.log('response after register auto-login == ' + res + ' json-formatted == ' + JSON.stringify(res));
        this.registerService.setLoggedChurch(res);
        this.showSuccess('Registration Successful \nYou will be redirected soon', 'success');
        // this.router.navigate(['']);
      },
      error => {
        this.showError('An Error Occurred while trying to sign you in, please check your network', 'error');
      });
  }

  ngOnDestroy(): void {
    if (this.busy != null) {
      this.busy.unsubscribe();
    }
  }

}



