import { Injectable } from '@angular/core';
import {CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router} from '@angular/router';
import { Observable } from 'rxjs/Observable';
import {UserAuthService} from '../service/user-auth.service';

@Injectable()
export class AdminAuthGuard implements CanActivate {

  // loggedChurch: ChurchResponse;
  adminStatus: boolean;
  constructor (private userService: UserAuthService, private route: Router) {
  }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | boolean {
    // return false;
    // this.loggedChurch = this.userService.getLoggedChurch();
    this.adminStatus = this.userService.getIsAdmin();
    console.log('logged admin status inside Guard == ' + this.adminStatus);
    if (!this.adminStatus) {
      this.route.navigate(['login']);
    } else {
      return true;
    }
  }
}



