import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Subscription} from 'rxjs/Subscription';
import {ThemeModel} from '../shared/model/ThemeModel';
import {MenuService} from '../shared/service/menu.service';
import {FileModel} from '../shared/model/FileModel';
import {TextContentModel} from '../shared/model/TextContentModel';

@Component({
  selector: 'app-app-root',
  templateUrl: './app-root.component.html',
  styleUrls: ['./app-root.component.css']
})
export class AppRootComponent implements OnInit {

  menuSelected2: boolean;
  listItems: string[] = [];
  listForm: FormGroup;
  audioForm: FormGroup;
  videoForm: FormGroup;
  textAreaForm: FormGroup;
  urlForm: FormGroup;
  mThemeModel: ThemeModel;
  sub: Subscription;
  aa: string;

  constructor(private fb: FormBuilder, private menuService: MenuService) {
    this.listForm = this.fb.group({
      listField: [null]
    });
    this.audioForm = this.fb.group({
      audioFileName: [null],
      audioFileUrl: ['http://']
    });
    this.videoForm = this.fb.group({
      videoFileName: [null],
      videoFileUrl: ['http://']
    });
    this.textAreaForm = this.fb.group({
      textAreaTitle: [null],
      textAreaTxt: [null]
    });
    this.urlForm = this.fb.group({
      urlTitle: [null],
      urlAddress: ['http://']
    });
  }

  ngOnInit() {
  }


}



