import {Component, OnDestroy, OnInit, ViewContainerRef} from '@angular/core';
import {AppInfoService, BuildAppRes} from '../../../shared/service/app-info.service';
import {MenuService} from '../../../shared/service/menu.service';
import {AppModel} from '../../../shared/model/AppModel';
import {Subscription} from 'rxjs/Subscription';
import {UpdImgRes} from '../../../shared/service/theme.service';
import {NotifyService} from '../../../shared/service/notify.service';
import {Router} from '@angular/router';



@Component({
  selector: 'app-app-splash',
  templateUrl: './app-splash.component.html',
  styleUrls: ['./app-splash.component.css']
})
export class AppSplashComponent implements OnInit, OnDestroy {

  mFile: any;
  selectedSplash: File;
  selectedIcon: File;
  appModel: AppModel;
  splashSelected: boolean;
  splashUploaded: boolean;
  iconSelected: boolean;
  iconUploaded: boolean;
  busy: Subscription;
  splashUrl: UpdImgRes;
  iconUrl: UpdImgRes;
  appId: BuildAppRes;

  constructor(private ais: AppInfoService, private notify: NotifyService,
              private vRef: ViewContainerRef, private route: Router) { }

  ngOnInit() {
    this.appModel = new AppModel;
    this.appId = new BuildAppRes;
    this.appModel = this.ais.getAppInfo();
    this.splashSelected = false;
    this.splashUploaded = false;
    this.iconSelected = false;
    this.iconUploaded = false;
  }

  ngOnDestroy(): void {
    if (this.busy != null) {
      this.busy.unsubscribe();
    }
  }


  imageUpload(event: any) {
    console.log('file response after upload == ' + event.file.name);
    this.selectedSplash = event.file;
    this.splashSelected = true;
  }
  iconUpload(event: any) {
    console.log('file response after upload == ' + event.file.name);
    this.selectedIcon = event.file;
    this.iconSelected = true;
  }

  uploadSplash () {
    this.busy = this.ais.uploadAppSplash(this.selectedSplash).subscribe(res => {
      this.splashUrl = res.image_path;
      this.appModel.splash_url = this.splashUrl;
      this.splashUploaded = true;
      this.splashSelected = false;
      this.notify.showSuccess('Splash Image upload successful', 'SUCCESS', this.vRef);
    }, err => {
      this.notify.showError(err, 'An Error Occured', this.vRef);
    });
  }

  uploadIcon () {
    this.busy = this.ais.uploadAppIcon(this.selectedIcon).subscribe(res => {
    this.iconUrl = res.image_path;
    this.appModel.icon_url = this.iconUrl;
    this.iconUploaded = true;
    this.iconSelected = false;
      this.notify.showSuccess('Icon upload successful', 'SUCCESS', this.vRef);
  }, err => {
    this.notify.showError(err, 'An Error Occured', this.vRef);
  });
  }

  buildApp() {
    console.log('appinfo of rady app == ' + JSON.stringify(this.appModel));
    this.ais.buildMyApp(this.appModel).subscribe(res => {
      // this.appId = res.appid;
      console.log('Response on communicating with Jenkins == ' + JSON.stringify(res));
      this.returnHome();
    });
  }

  returnHome () {
    this.notify.showSuccess('We are working on your App\nwe will mail you the created app soon', 'SUCCESS', this.vRef);
    /*setTimeout(() => {
      this.route.navigate(['']);
    }, 2000);*/
  }

}



