import {Component, OnDestroy, OnInit, ViewContainerRef} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {AppModel} from '../../shared/model/AppModel';
import {AppInfoService} from '../../shared/service/app-info.service';
import {ThemeResponse} from '../../shared/model/ThemeResponse';
import {ThemeService} from '../../shared/service/theme.service';
import {Subscription} from 'rxjs/Subscription';
import {NotifyService} from '../../shared/service/notify.service';
import {ThemeModel} from '../../shared/model/ThemeModel';
import {UserAuthService} from '../../shared/service/user-auth.service';
import {ChurchResponse} from '../../shared/model/ChurchResponse';

class AppIdResponse {
  appid: any;
}

@Component({
  selector: 'app-select-theme',
  templateUrl: './select-theme.component.html',
  styleUrls: ['./select-theme.component.css']
})
export class SelectThemeComponent implements OnInit, OnDestroy {

  createForm: FormGroup;
  setCheckedTheme: boolean;
  /*app_name: '';
  app_tagline: '';
  app_theme: '';*/
  mAppModel: AppModel;
  allThemes: ThemeResponse;
  busy: Subscription;
  checkIndex: number;
  selectedThemeId: any;
  mAppIdRes: AppIdResponse;
  churchResponse: ChurchResponse;

  constructor(public fb: FormBuilder, public route: Router, public aRoute: ActivatedRoute,
              public ais: AppInfoService, private themeService: ThemeService,
              private notify: NotifyService, private vRef: ViewContainerRef, private loggedChurch: UserAuthService) {
    this.createForm = this.fb.group({
      appName: [null, Validators.required],
      appTagLine: [null, Validators.required]}
    );
    /*this.mAppModel.appName = this.app_name;
    this.mAppModel.appTagLine = this.app_tagline;
    */
  }

  ngOnInit() {
    this.setCheckedTheme = false;
    this.mAppModel = new AppModel;
    this.allThemes = new ThemeResponse;
    this.mAppIdRes = new AppIdResponse;
    this.loggedChurch.getLoggedChurch().subscribe(res => {
      this.churchResponse = res;
    });
    // this.mAppModel.appTheme = 'default';
    this.fetchAllThemes();
  }

  ngOnDestroy(): void {
    if (this.busy != null) {
      this.busy.unsubscribe();
    }
  }

  changeCheckStatus(ind: any, themeId: any) {
    for (let a = 0; a < this.allThemes.themes.length; a++) {
      if (ind === a) {
        this.setCheckedTheme = true;
        this.checkIndex = a;
        this.selectedThemeId = themeId;
      } else {
        // this.setCheckedTheme = false;
      }
    }
  }


  fetchAllThemes () {
    this.busy = this.themeService.getUpdatedTheme().subscribe(res => {
      this.allThemes = res;
    }, err => {
      this.notify.showError('Please Check your Network and try again', 'Error', this.vRef);
    });
  }

  gotoBuilder() {
    this.mAppModel.themeid = this.selectedThemeId;
    this.mAppModel.creator_email = this.churchResponse.user.email;
    // this.ais.updateAppInfo(this.mAppModel);
    // fetch app id
    this.busy = this.ais.fetchAppId(this.mAppModel.name, this.mAppModel.app_tag_line, this.mAppModel.themeid,
      this.mAppModel.creator_email)
      .subscribe(res => {
        this.mAppIdRes = res;
        this.mAppModel.appId = this.mAppIdRes.appid;
        // update app info service
        this.ais.updateAppInfo(this.mAppModel);
        this.route.navigate(['build'], {relativeTo: this.aRoute.parent});
      }, err => {
        console.log('error from endpoint == ' + err);
        this.notify.showError('Please Check your Network and try again ' + '\n' + err, 'An Error occurred', this.vRef);
      });
    console.log('appinfo inside ' + this.mAppModel.name + ' theme id == ' + this.selectedThemeId);
  }

}



