import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Subscription} from 'rxjs/Subscription';
import {ThemeModel} from '../shared/model/ThemeModel';
import {MenuService} from '../shared/service/menu.service';
import {FileModel} from '../shared/model/FileModel';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {


  menuSelected2: boolean;
  listItems: string[] = [];
  listForm: FormGroup;
  audioForm: FormGroup;
  videoForm: FormGroup;
  textAreaForm: FormGroup;
  urlForm: FormGroup;
  mThemeModel: ThemeModel;
  sub: Subscription;
  aa: string;

  constructor(private fb: FormBuilder, private menuService: MenuService) {
    this.listForm = this.fb.group({
      listField: [null]
    });
    this.audioForm = this.fb.group({
      audioFileName: [null],
      audioFileUrl: ['http://']
    });
    this.videoForm = this.fb.group({
      videoFileName: [null],
      videoFileUrl: ['http://']
    });
    this.textAreaForm = this.fb.group({
      textAreaTitle: [null],
      textAreaTxt: [null]
    });
    this.urlForm = this.fb.group({
      urlTitle: [null],
      urlAddress: ['http://']
    });
  }

  ngOnInit() {
  }

  // send submenu list one to service
  pushListArray(listMass: string) {
    this.listItems = [];
    const array = listMass.split(',');
    for (let a = 0; a < array.length; a++) {
      this.aa = array[a].trim();
      this.listItems.push(this.aa);
    }
    this.menuService.setSubMenuArrayL1(this.listItems);
    this.resetModalForm(this.listForm);
  }

  // send submenu list two to service
  pushSubListArray(listMass: string) {
    console.log('pushed list == ' + listMass);
    this.listItems = [];
    const array = listMass.split(',');
    for (let a = 0; a < array.length; a++) {
      this.aa = array[a].trim();
      this.listItems.push(this.aa);
    }
    this.menuService.setSubMenuArrayL2(this.listItems);
    // this.listForm.reset();
    this.resetModalForm(this.listForm);
  }

  pushAudioModel (fileName, fileUrl, fileType) {
    const fileNew: FileModel = new FileModel;
    fileNew.name = fileName;
    // fileNew.description = fileDesc;
    fileNew.content = fileUrl;
    fileNew.fileType = fileType;
    // push new audioFile to service
    this.menuService.setObservableFile(fileNew);
    this.resetModalHttpForm(this.audioForm, 'audioFileUrl');
  }

  pushVideoModel(fileName, fileUrl, fileType) {
    const fileNew: FileModel = new FileModel;
    fileNew.name = fileName;
    // fileNew.description = fileDesc;
    fileNew.content = fileUrl;
    fileNew.fileType = fileType;
    // push new videoFile to service
    this.menuService.setObservableFile(fileNew);
    this.resetModalHttpForm(this.videoForm, 'videoFileUrl');
  }

  pushUrlModel(urlTitle, urlAddress, fileType) {
    const fileNew: FileModel = new FileModel;
    fileNew.name = urlTitle;
    fileNew.content = urlAddress;
    fileNew.fileType = fileType;
    // push new url to service
    this.menuService.setObservableFile(fileNew);
    this.resetModalHttpForm(this.urlForm, 'urlAddress');
  }

  pushTextModel(textTitle, txt, fileType) {
    const fileNew: FileModel = new FileModel;
    fileNew.name = textTitle;
    fileNew.content = txt;
    fileNew.fileType = fileType;
    // push new text to service
    this.menuService.setObservableFile(fileNew);
    this.resetModalForm(this.textAreaForm);
  }

  resetModalForm(form: FormGroup) {
    setTimeout(() => {
      form.reset();
    }, 2000);
  }

  resetModalHttpForm(form: FormGroup, controlName: string) {
    setTimeout(() => {
      form.reset();
      (<FormControl>form.controls[controlName]).setValue('http://', { onlySelf: true });
    }, 2000);
  }
}



