import { Injectable } from '@angular/core';
import {MenuModel} from '../model/MenuModel';
import 'rxjs/add/observable/throw';
import {Subject} from 'rxjs/Subject';
import {Observable} from 'rxjs/Observable';
import {ReplaySubject} from 'rxjs/ReplaySubject';
import {Http, RequestOptions, Headers, Response} from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import {AppSettings} from '../AppSettings';
import {MenuResponse} from '../model/MenuResponse';
import {SubMenuResponse2} from '../model/SubMenuResponse2';
import {FileModel} from '../model/FileModel';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

class FilePkg {
  fileModel: any;
  fileType: string;
}

@Injectable()
export class MenuService {

  menuModel: MenuModel;
  menu_post_url= 'http://api.smartapp.tk/api/v1/menu1/';
  private headers;
  private options;

  private subject: Subject<any> = new Subject();
  private menuSubject: ReplaySubject<any> = new ReplaySubject(1);
  private subjectL2: Subject<any> = new Subject();
  private fileSubject: Subject<any> = new Subject();
  // public subMenuObs: ReplaySubject<any> = new ReplaySubject(1);
  public subMenuObs: Subject<SubMenuResponse2> = new Subject();
  private menu1Obs: Subject<MenuResponse> = new Subject();

  constructor(protected http: Http) {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    this.headers.append('Accept', 'application/json');
    this.options = new RequestOptions({
      headers: this.headers});
    this.menuModel = new MenuModel;
  }

  setMenuModel(name: string, icon_text: string, content: string) {
    this.menuModel.menu_name = name;
    this.menuModel.menu_icon = icon_text;
    this.menuModel.menu_content = content;
    this.menuSubject.next(this.menuModel);
  }

  getMenuModel() {
    return this.menuSubject.asObservable();
  }

  setSubMenuArrayL1(menuArr: string[]) {
    this.subject.next(menuArr);
  }

  getSubMenuArrayL1(): Observable<any> {
    return this.subject.asObservable();
  }

  setSubMenuArrayL2(menuArr2: string[]) {
    this.subjectL2.next(menuArr2);
  }

  getSubMenuArrayL2(): Observable<any> {
    return this.subjectL2.asObservable();
  }

  setObservableFile(file: FileModel) {
    this.fileSubject.next(file);
  }

  getObservableFile(): Observable<FileModel> {
    return this.fileSubject.asObservable();
  }

  postMenuToServer(menu: any): Observable<MenuResponse> {
    const body = JSON.stringify(menu);
    console.log('POSTMENU in menuService strinigi body == ' + body);
    return this.http.post(AppSettings.API_ENDPOINT + '/menu1', body, this.options)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  postSubMenuToServer(menu: any): Observable<SubMenuResponse2> {
    const body = JSON.stringify(menu);
    console.log('POSTMENU in menuService strinigi body == ' + body);
    return this.http.post(AppSettings.API_ENDPOINT + '/submenu2', body, this.options)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  deleteMenuFromServer (id: any): Observable<Response> {
      return this.http.delete(AppSettings.API_ENDPOINT + '/submenu1/' + id) // ...using put request
        .map((res: Response) => res.json()) //
        .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  deleteSubmenu2FromServer (id: any): Observable<Response> {
      return this.http.delete(AppSettings.API_ENDPOINT + '/submenu2/' + id) // ...using put request
        .map((res: Response) => res.json()) //
        .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }


  setObservableSub(submenu: any) {
    this.postSubMenuToServer(submenu).subscribe(res => {
      this.subMenuObs.next(res);
    }, err => {
      this.subMenuObs.error(err);
      // this.subMenuObs = error;
    });
  }

  getSubmenuObs() {
    return this.subMenuObs.asObservable();
  }
  setMenu1Obs(menu: any) {
    this.postMenuToServer(menu).subscribe(res => {
      this.menu1Obs.next(res);
      console.log('menu response in service == ' + JSON.stringify(res));
    }, err => {
      this.menu1Obs.error(err);
      // this.subMenuObs = error;
    });
  }

  getMenu1Obs() {
    return this.menu1Obs.asObservable();
  }
}



