import { Component, OnInit } from '@angular/core';
import {AppInfoService, StatusToolBar} from '../../shared/service/app-info.service';

@Component({
  selector: 'app-design',
  templateUrl: './design.component.html',
  styleUrls: ['./design.component.css']
})
export class DesignComponent implements OnInit {

  colorIndex: number;
  statusTB: StatusToolBar;

  darkNLight: any[] = [{mName: 'Light', mBackground: '#ffffff', mText: '#333333', mSubTitle: '#999999', mBackBg: '#ececec'},
    {mName: 'Dark', mBackground: '#333333', mText: '#ffffff', mSubTitle: '#e5e5e5', mBackBg: '#444444'}];

  constructor(private ais: AppInfoService) { }

  ngOnInit() {
    this.ais.getSelectedAppColor().subscribe(res => {
      this.colorIndex = res;
    });
    this.ais.getSelectedAppStatusToolbar().subscribe(res => {
      this.statusTB = res;
    });
  }

}



