import {Component, OnDestroy, OnInit, ViewContainerRef} from '@angular/core';
import {ActivatedRoute, Route, Router} from '@angular/router';
import {AppInfoService} from '../../shared/service/app-info.service';
import {UserAppResponse} from '../../shared/model/UserAppResponse';
import {Subscription} from 'rxjs/Subscription';
import {ChurchResponse} from '../../shared/model/ChurchResponse';
import {UserAuthService} from '../../shared/service/user-auth.service';
import {NotifyService} from '../../shared/service/notify.service';

@Component({
  selector: 'app-create-app',
  templateUrl: './create-app.component.html',
  styleUrls: ['./create-app.component.css']
})
export class CreateAppComponent implements OnInit, OnDestroy {

  userApps: UserAppResponse;
  busy: Subscription;
  churchResponse: ChurchResponse;
  email: any;
  hasApps: boolean;

  constructor(public router: Router, public aroute: ActivatedRoute, private ais: AppInfoService,
              private loggedChurch: UserAuthService, private notify: NotifyService, private vRef: ViewContainerRef) { }

  ngOnInit() {
    this.busy = this.loggedChurch.getLoggedChurch().subscribe(res => {
      this.email = res.user.email;
    });
    this.hasApps = false;
    this.getAllUserApps();
  }

  ngOnDestroy(): void {
    if (this.busy != null) {
      this.busy.unsubscribe();
    }
  }

  startCreator() {
    this.router.navigate(['select'], {relativeTo: this.aroute.parent});
  }

  getAllUserApps() {
    this.busy = this.ais.fetchUserApps(this.email).subscribe(res => {
      this.userApps = res;
      if (res.apps.length <= 0) {
        this.hasApps = false;
      } else {
        this.hasApps = true;
      }
    });
  }

  deleteApp(appId: any) {
    this.busy = this.ais.deleteApp(appId).subscribe(res => {
      console.log('ON success of app deletion == ' + JSON.stringify(res));
      this.showSuccessDel();
    }, err => {
      this.notify.showError('Please check your network and try again' + '\n' + err, 'An Error Occurred', this.vRef);
    });
  }

  showSuccessDel () {
    this.notify.showSuccess('App successfully removed', 'SUCCESS', this.vRef);
    this.getAllUserApps();
  }

}



