import { Injectable } from '@angular/core';
import {AppModel} from '../model/AppModel';
import 'rxjs/add/observable/throw';
import {Http, RequestOptions, Response, Headers} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import {UpdImgRes} from './theme.service';
import {AppSettings} from '../AppSettings';
import {UserAppResponse} from '../model/UserAppResponse';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Subject} from 'rxjs/Subject';
import {ReplaySubject} from 'rxjs/ReplaySubject';

class AppIdResponse {
  appid: any;
}

export class BuildAppRes {
  status: any;
  // appid: any;
}

export class StatusToolBar {
  statusColor: any;
  toolbarColor: any;
}

@Injectable()
export class AppInfoService {

  mAppInfo: AppModel;
  partial_app_url= AppSettings.API_ENDPOINT + '/app/partial';
  private headers;
  private options;
  mAppRes: BuildAppRes;
  private img_upload_url= AppSettings.API_ENDPOINT + '/image/upload';
  private app_url= AppSettings.API_ENDPOINT + '/app/';
  private android_app_builder= AppSettings.API_ENDPOINT + '/android/';
  private user_apps_url = AppSettings.API_ENDPOINT + '/app/creator/'; // append email

  public space: Subject<number> = new BehaviorSubject<number>(0);
  public statusTool: Subject<StatusToolBar> = new ReplaySubject<StatusToolBar>(1);
  // public statusTool: Subject<StatusToolBar> = new ReplaySubject<StatusToolBar>(1);

  constructor(protected http: Http) {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    this.headers.append('Accept', 'application/json');
    this.options = new RequestOptions({
      headers: this.headers});
    this.mAppRes = new BuildAppRes;
  }

  updateAppInfo (appmodel: any) {
    /*this.mAppInfo.appName = appName;
    this.mAppInfo.appTagLine = appTagLine;
    this.mAppInfo.appTheme = appTheme;*/
    this.mAppInfo = appmodel;
  }

  getAppInfo () {
    return this.mAppInfo;
  }

  fetchUserApps (email: any): Observable<UserAppResponse> {
    return this.http.get(this.user_apps_url + email)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  fetchAppId(appName: any, appTagLine: any, themeId: any, creator_email: any): Observable<AppIdResponse> {
    const app_body = {'name': appName, 'app_tag_line': appTagLine, 'themeid': themeId, 'creator_email': creator_email};
    const body = JSON.stringify(app_body);
    console.log('json app body == ' + body);
    return this.http.post(this.partial_app_url, body, this.options)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  uploadAppSplash(file: File): Observable<UpdImgRes> {
    const formData: FormData = new FormData();
    formData.append('image', file, file.name);
    const headers = new Headers();

    /** No need to include Content-Type in Angular 4 */
    headers.append('enctype', 'multipart/form-data');
    const options = new RequestOptions({ headers: headers });
    return this.http.post(this.img_upload_url, formData)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  uploadAppIcon(file: File): Observable<UpdImgRes> {
    const formData: FormData = new FormData();
    formData.append('image', file, file.name);
    const headers = new Headers();

    /** No need to include Content-Type in Angular 4 */
    headers.append('enctype', 'multipart/form-data');
    const options = new RequestOptions({ headers: headers });
    return this.http.post(this.img_upload_url, formData)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  buildMyApp(appModel: AppModel) {
    const mAppmodel = {
      'name': appModel.name,
      'app_tag_line': appModel.app_tag_line,
      'themeid': appModel.themeid,
      'icon_url': appModel.icon_url,
      'splash_url': appModel.splash_url,
      'creator_email': appModel.creator_email,
      'color_primary': appModel.color_primary,
      'color_primary_dark': appModel.color_primary_dark,
      'color_background': appModel.color_background,
      'text_color': appModel.text_color
    };
    const body = JSON.stringify(mAppmodel);
    // let appId = '';
    /*return this.http.post(this.app_url, body, this.options)
      .map((res: Response) => res.json())
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));*/

    console.log('appbody before final build == ' + body);

    return this.http.put(this.app_url + appModel.appId, body, this.options)
      .map((res: Response) => {
        this.mAppRes = res.json();
        // appId = this.mAppRes.appid;
        console.log('gotten APP STATUS in service before Flatmap == ' + this.mAppRes.status);
      })
      .flatMap((mBody) => {
        console.log('MBody in flatmap ##APPINFO == ' + mBody + ' jsonified body == ' + JSON.stringify(mBody));
        return this.http.get(this.android_app_builder + appModel.appId)
          .map((resp: Response) => resp.json())
          .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
      }) // ...and calling .json() on the response to return data
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }


  // Delete app
  deleteApp (id: string): Observable<Response> {
    return this.http.delete(this.app_url + id) // ...using put request
      .map((res: Response) => res.json()) //
      .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }


  setSelectedAppColor(color: number) {
    this.space.next(color);
  }

  getSelectedAppColor() {
    return this.space.asObservable();
  }

  setSelectedAppStatusToolbar(color: StatusToolBar) {
    this.statusTool.next(color);
  }

  getSelectedAppStatusToolbar() {
    return this.statusTool.asObservable();
  }

}




