import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppComponent} from './app.component';
import {LoginComponent} from './login/login.component';
import {RegisterComponent} from './register/register.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {RouterModule} from '@angular/router';
import {HttpModule} from '@angular/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {SidebarComponent} from './sidebar/sidebar.component';
import {NavbarComponent} from './shared/navbar/navbar.component';
import {FooterComponent} from './shared/footer/footer.component';
import {CreateAppComponent} from './dashboard/create-app/create-app.component';
import {PublishComponent} from './dashboard/publish/publish.component';
import {SettingsComponent} from './dashboard/settings/settings.component';
import {SelectThemeComponent} from './dashboard/select-theme/select-theme.component';
import {EditAppComponent} from './dashboard/edit-app/edit-app.component';
import {UpdateContentComponent} from './dashboard/update-content/update-content.component';
import {AppRootComponent} from './app-root/app-root.component';
import {PagenotfoundComponent} from './pagenotfound/pagenotfound.component';
import {BuilderComponent} from './builder/builder.component';
import {AppInfoService} from './shared/service/app-info.service';
import {BuilderSubOneComponent} from './builder/builder-sub-one/builder-sub-one.component';
import {BuilderSubTwoComponent} from './builder/builder-sub-two/builder-sub-two.component';
import {BuilderSubThreeComponent} from './builder/builder-sub-three/builder-sub-three.component';
import {BuilderSubFourComponent} from './builder/builder-sub-four/builder-sub-four.component';
import {BuilderSubFiveComponent} from './builder/builder-sub-five/builder-sub-five.component';
import {UserAuthService} from './shared/service/user-auth.service';
import {BusyModule} from 'angular2-busy';
import {ToastModule} from 'ng2-toastr';
import {NoopAnimationsModule} from '@angular/platform-browser/animations';
import {AuthGuard} from './shared/guard/auth.guard';
import {MenuService} from './shared/service/menu.service';
import {AdmindashboardComponent} from './admindashboard/admindashboard.component';
import {AdminAuthGuard} from './shared/guard/admin-auth.guard';
import {AdminSubComponent} from './admindashboard/admin-sub/admin-sub.component';
import {AdminThemeComponent} from './admindashboard/admin-theme/admin-theme.component';
import {AdminUserComponent} from './admindashboard/admin-user/admin-user.component';
import {AdminAppsComponent} from './admindashboard/admin-apps/admin-apps.component';
import {ThemeService} from './shared/service/theme.service';
import {ImageUploadModule} from 'angular2-image-upload';
import {NotifyService} from './shared/service/notify.service';
import { DesignComponent } from './dashboard/design/design.component';
import { AppColorComponent } from './dashboard/design/app-color/app-color.component';
import { AppSplashComponent } from './dashboard/design/app-splash/app-splash.component';
import {ColorPickerModule} from 'angular4-color-picker';
import {HashLocationStrategy, LocationStrategy} from '@angular/common';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    DashboardComponent,
    SidebarComponent,
    NavbarComponent,
    FooterComponent,
    CreateAppComponent,
    PublishComponent,
    SettingsComponent,
    SelectThemeComponent,
    EditAppComponent,
    UpdateContentComponent,
    AppRootComponent,
    PagenotfoundComponent,
    BuilderComponent,
    BuilderSubOneComponent,
    BuilderSubTwoComponent,
    BuilderSubThreeComponent,
    BuilderSubFourComponent,
    BuilderSubFiveComponent,
    AdmindashboardComponent,
    AdminSubComponent,
    AdminThemeComponent,
    AdminUserComponent,
    AdminAppsComponent,
    DesignComponent,
    AppColorComponent,
    AppSplashComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    NoopAnimationsModule,
    HttpModule,
    BusyModule,
    ColorPickerModule,
    ImageUploadModule.forRoot(),
    ToastModule.forRoot(),
    RouterModule.forRoot([
      {
        path: '',
        // canActivate: [AuthGuard],
        component: DashboardComponent,
        children: [
          {path: '', redirectTo: 'user', pathMatch: 'full'},
          {
            path: 'user', component: AppRootComponent,
            children: [
              {path: '', redirectTo: 'app', pathMatch: 'full'},
              {path: 'app', component: CreateAppComponent},
              {path: 'select', component: SelectThemeComponent},
              {
                path: 'build', component: BuilderComponent,
                children: [
                  {path: '', redirectTo: 'page_one', pathMatch: 'full'},
                  {path: 'page_one', component: BuilderSubOneComponent},
                  {path: 'page_two', component: BuilderSubTwoComponent},
                  {path: 'page_three', component: BuilderSubThreeComponent},
                  {path: 'page_four', component: BuilderSubFourComponent},
                  {path: 'page_five', component: BuilderSubFiveComponent}
                ]
              },
              {path: 'design', component: DesignComponent,
                children: [
                  {path: '', redirectTo: 'color', pathMatch: 'full'},
                {path: 'color', component: AppColorComponent},
                {path: 'icon', component: AppSplashComponent}
              ]},
            ]
          },
          // {path: 'create', component: SelectThemeComponent},
        ]
      },
      {
        path: 'admin',
        component: AdmindashboardComponent,
        canActivate: [AdminAuthGuard],
        children: [
          {path: '', redirectTo: 'dashboard', pathMatch: 'full'},
          {
            path: 'dashboard', component: AdminSubComponent,
            children: [
              {path: '', redirectTo: 'users', pathMatch: 'full'},
              {path: 'users', component: AdminUserComponent},
              {path: 'themes', component: AdminThemeComponent},
              {path: 'apps', component: AdminAppsComponent}
            ]
          }
        ]
      },
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: 'register',
        component: RegisterComponent
      },
      {
        path: '**',
        component: PagenotfoundComponent
      }
    ])
  ],
  providers: [AppInfoService, UserAuthService, AuthGuard, AdminAuthGuard, MenuService, ThemeService, NotifyService,
    {provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule {
}



