import {Component, Input, OnInit} from '@angular/core';
import {ROUTES} from '../../sidebar/sidebar-routes.config';
import {MenuType} from '../../sidebar/sidebar.metadata';
import {Router, ActivatedRoute} from '@angular/router';
import {Location, LocationStrategy, PathLocationStrategy} from '@angular/common';
import {ChurchResponse} from '../model/ChurchResponse';
import {UserAuthService} from '../service/user-auth.service';

@Component({
  moduleId: module.id,
  selector: 'app-navbar',
  templateUrl: 'navbar.component.html'
})

export class NavbarComponent implements OnInit {

  @Input() role: any;
  private listTitles: any[];
  location: Location;
  churchInfo: ChurchResponse;
  churchEmail: string;

  constructor(location: Location, private churchService: UserAuthService) {
    this.location = location;
  }

  ngOnInit() {
    this.listTitles = ROUTES.filter(listTitle => listTitle.menuType !== MenuType.BRAND);

    this.churchInfo = new ChurchResponse;
    // this.churchInfo = this.churchService.getLoggedChurch();
    this.churchService.getLoggedChurch().subscribe(res => {
      this.churchInfo = res;
      this.churchEmail = this.churchInfo.user.email;
    });
  }

  getTitle() {
    let titlee = this.location.prepareExternalUrl(this.location.path());
    if (titlee.charAt(0) === '#') {
      titlee = titlee.slice(2);
    }
    for (let item = 0; item < this.listTitles.length; item++) {
      if (this.listTitles[item].path === titlee) {
        return this.listTitles[item].title;
      }
    }
    return 'Dashboard';
  }

  getPath() {
    // console.log(this.location);
    return this.location.prepareExternalUrl(this.location.path());
  }
}



