import { Component, OnInit } from '@angular/core';
import {AppModel} from '../shared/model/AppModel';
import {AppInfoService} from '../shared/service/app-info.service';
import {MenuService} from '../shared/service/menu.service';
import {FormBuilder, FormGroup} from '@angular/forms';
import {Subscription} from 'rxjs/Subscription';
import {ThemeService} from '../shared/service/theme.service';
import {ThemeModel} from '../shared/model/ThemeModel';

@Component({
  selector: 'app-builder',
  templateUrl: './builder.component.html',
  styleUrls: ['./builder.component.css']
})
export class BuilderComponent implements OnInit {

  mAppmodel: AppModel;
  appName: string;
  listForm: FormGroup;
  mThemeModel: ThemeModel;
  sub: Subscription;

  constructor(public ais: AppInfoService, private menuService: MenuService,
              private fb: FormBuilder, private themeService: ThemeService) {
    this.listForm = this.fb.group({
      listField: [null]
    });
  }

  ngOnInit() {
    this.mAppmodel = new AppModel;
    this.mAppmodel = this.ais.getAppInfo();
    this.mThemeModel = new ThemeModel;
    if (this.mAppmodel != null) {
    console.log('retrieved app info ID == ' + this.mAppmodel.appId + ' theme Id == ' + this.mAppmodel.themeid);
    }
    // this.setMenuModel('Home', 'home', 'menu contents');
    this.fetchThemeMenu();
  }

  setMenuModel(name: string, icon: string, content: string) {
    this.menuService.setMenuModel(name, icon, content);
    console.log('passed menu name inside BUILDER == ' + name);
  }

  fetchThemeMenu() {
    if (this.mAppmodel != null) {
      this.sub = this.themeService.getThemeById(this.mAppmodel.themeid).subscribe(res => {
        this.mThemeModel = res.theme;
        this.setMenuModel(this.mThemeModel.first_menu, 'home', 'menu contents');
      });
    }
  }
/*
  pushListArray(listmass: string) {
    console.log('pushed list == ' + listmass);
    // this.menuSelected2 = true;
    this.listItems = [];
    const array = listmass.split(',');
    for (let a = 0; a < array.length; a++) {
      this.listItems.push(array[a]);
    }
    this.menuService.setSubMenuArrayL1(this.listItems);
    this.listForm.reset();
    /!*console.log('updated pushed array == ' + this.listItems);
    this.menuSelected2 = true;*!/
  }*/

}



