import {Component, OnDestroy, OnInit, ViewContainerRef} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Subscription} from 'rxjs/Subscription';
import {UserAuthService} from '../shared/service/user-auth.service';
import {ToastsManager} from 'ng2-toastr';
import {Router} from '@angular/router';
import {ChurchResponse} from '../shared/model/ChurchResponse';

@Component({
  moduleId: 'module.id',
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit, OnDestroy {

  loginForm: FormGroup;
  email: string;
  password: string;
  busy: Subscription;
  cUser: ChurchResponse;
  hasErrors: boolean;
  errorText: any;

  constructor(public fb: FormBuilder, private loginService: UserAuthService,
              private toastr: ToastsManager, vRef: ViewContainerRef, private router: Router) {
    this.loginForm = this.fb.group({
      emailField: [null, [Validators.required, Validators.email]],
      // emailField: [null, [Validators.required, Validators.pattern('[^ @]*@[^ @]*')]],
      pwdField: [null, [Validators.required, Validators.minLength(8)]]
    });
    this.toastr.setRootViewContainerRef(vRef);
  }

  ngOnInit() {
    this.loginService.getLoggedChurch().subscribe(res => {
      this.cUser = res;
    });
    this.hasErrors = false;
  }

  login() {
    console.log('logged church email == ' + this.email + ' password == ' + this.password);
    this.hasErrors = false;
    this.busy = this.loginService.loginUser(this.email, this.password).subscribe(res => {
      console.log('logged church == ' + res.user.role);
      this.loginForm.disable();
      this.loginService.setLoggedChurch(res);
      this.showSuccess('Login Successful \nYou will be redirected soon', 'success');
    }, error => {
      this.showError('Login not successful', 'An error occurred');
      this.hasErrors = true;
      this.errorText = error;
    });
  }

  showSuccess(text: string, title: string) {
    this.toastr.success(text, title, {toastLife: 3000, showCloseButton: false});
    setTimeout(() => {
      if (this.cUser.user.role === 1) {
        this.loginService.setIsAdmin(this.cUser.user.role);
        this.router.navigate(['./admin']);
      } else {
        this.router.navigate(['']);
      }
    }, 3000);  // 5s
    // this.router.navigate(['']);
  }


  showError(text: string, title: string) {
    this.toastr.error(text, title);
  }

  ngOnDestroy(): void {
    if (this.busy != null) {
      this.busy.unsubscribe();
    }
  }
}



