import {Injectable, ViewContainerRef} from '@angular/core';
import {ToastsManager} from 'ng2-toastr';

@Injectable()
export class NotifyService {
  public vRef: ViewContainerRef;
  constructor(private toastr: ToastsManager) {
  }

  showSuccess(text: string, title: string, vmRef: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vmRef);
    this.toastr.success(text, title, {toastLife: 3000, showCloseButton: false});
    // this.fetchThemes();
  }

  showError(text: string, title: string, vmRef: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vmRef);
    this.toastr.error(text, title);
  }

}



